.componentbox__page {
  position: absolute;
  z-index: 2;
  top: -9999px;
  bottom: -9999px;
  left: -9999px;
  right: -9999px;
  margin: auto;
  overflow: hidden;
  height: 40vh;
  width: 40vw;
  font-size: 16px;
  transition: all .5s;
  cursor: pointer; }
  .componentbox__page:hover .page__title {
    font-size: 2.5em; }
  .componentbox__page .page__navbar {
    height: 5%;
    width: 100%;
    position: absolute;
    left: 0px;
    min-height: 2em;
    top: 0px;
    color: white;
    z-index: 2; }
    .componentbox__page .page__navbar .navbar__menuicon {
      position: absolute;
      right: 5px;
      top: 50%;
      transform: translateY(-50%); }
    .componentbox__page .page__navbar--indigo {
      background-color: indigo; }
    .componentbox__page .page__navbar--green {
      background-color: #424254; }
    .componentbox__page .page__navbar--gray {
      background-color: #5C5C5C; }
    .componentbox__page .page__navbar--brown {
      background-color: #281800; }
    .componentbox__page .page__navbar--none {
      display: none; }
  .componentbox__page .page__content {
    pointer-events: none;
    overflow: hidden;
    height: 100%;
    position: relative;
    width: 100%;
    z-index: 1; }
    .componentbox__page .page__content .placeholder {
      height: 5%;
      width: 100%;
      min-height: 2em; }
    .componentbox__page .page__content .center {
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translateX(-50%) translateY(-50%); }
      @media screen and (max-width: 768px) {
        .componentbox__page .page__content .center {
          left: 0px;
          top: 2em;
          transform: initial; } }
  .componentbox__page .page__title {
    position: absolute;
    left: 0px;
    bottom: 0px;
    transition: all .25s;
    width: 100%;
    display: block;
    color: white;
    font-size: 2em;
    padding: 10px;
    animation-delay: 1s;
    background-color: #262626;
    animation-fill-mode: forwards;
    width: 100%;
    z-index: 2; }
    .componentbox__page .page__title.hidden {
      animation: hide 1s ease-in-out;
      display: none; }
    .componentbox__page .page__title.shown {
      animation: show 1s ease-in-out; }

